.profile {
    min-height: 120px;
    max-width: 880px;
    margin: 0 auto;
    padding: 40px 0 51px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

@media screen and (max-width: 670px) {
    .profile {
        flex-direction: column;
        padding: 42px 0 36px;
    }
}